import { useState } from 'react';
import { toast } from 'react-toastify';
import { useSelector } from 'react-redux';

const useRevertBet = () => {
	const gameObj = useSelector((state) => state.game);
	const { userData } = gameObj;
	const { wallet: userWallet } = userData || {};

	const [selectedBets, setSelectedBets] = useState([]);
	const [prevSelectedBet, setPrevSelectedBet] = useState([]);

	const addBet = (betAmount, type, value) => {
		if (!localStorage.getItem('usertoken')) {
			toast.error('please login then you can bet !', {
				position: 'top-right',
				autoClose: 5000,
				hideProgressBar: false,
				closeOnClick: true,
				pauseOnHover: true,
				draggable: true,
				progress: undefined,
				toastId: 1,
			});
			return;
		}

		if (betAmount <= 0) {
			toast.error('bet amount should be greater then 0', {
				position: 'top-right',
				autoClose: 5000,
				hideProgressBar: false,
				closeOnClick: true,
				pauseOnHover: true,
				draggable: true,
				progress: undefined,
				toastId: 1,
			});
			return;
		}

		if (!userWallet || betAmount > userWallet) {
			toast.error('amount is not sufficient to make bet!', {
				position: 'top-right',
				autoClose: 5000,
				hideProgressBar: false,
				closeOnClick: true,
				pauseOnHover: true,
				draggable: true,
				progress: undefined,
				toastId: 1,
			});
			return;
		}

		setSelectedBets((prev) => {
			return [...prev, { betAmount, type, value }];
		});
		let element = document.getElementById('coinsound');
		if (element) {
			element.play();
		}
	};

	const resetAllBet = () => {
		const prevBets = [...selectedBets];
		setPrevSelectedBet(() => {
			return [...prevBets];
		});
		setSelectedBets(() => []);
	};

	const revertResetBets = () => {
		if (prevSelectedBet.length) {
			const prevBets = [...prevSelectedBet];
			setSelectedBets(() => {
				return [...prevBets];
			});
			setPrevSelectedBet(() => []);
		}
	};

	const resetOneBet = () => {
		setSelectedBets(() => {
			const prevBets = [...selectedBets];
			prevBets.pop();
			return [...prevBets];
		});
	};

	const resetAfterBet = () => {
		setPrevSelectedBet(() => []);
		setSelectedBets(() => []);
	};

	return [
		selectedBets,
		prevSelectedBet,
		addBet,
		resetAllBet,
		revertResetBets,
		resetOneBet,
		resetAfterBet,
	];
};

export default useRevertBet;
